import axios from "axios";
import React, { useState , useEffect } from "react";
import bronze from "../../../assets/bronze.png";

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { Button, MenuItem, TextField } from "@mui/material";
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';

export default function Profile() {

    const userid = window.location.pathname.split("/")[3];
    const [value, setValue] = useState(new Date());
    const [userName,setUserName]=useState("")
    const [email,setEmail]=useState("")
    const [birthday,setBirthday]=useState("")
    const [user,setUser]=useState({})
    const [instruments,setInstruments]=useState([])
const getUser=async()=>{
    const res=await axios.get("http://localhost:3000/spacetune/api/user/"+userid);
    console.log(res.data.content)
    setUser(res.data.content)
    setUserName(res.data.content.userName)
    setEmail(res.data.content.email)
    setBirthday(res.data.content.birthday)
}
useEffect(() => {
    getUser()
}, []);
    return (
        <div>
              <div className="card card-body mb-4 p-4 text-center box grid place-items-center" >
      <h1 className="display-4 text-center">
        Settings ⚙️
      </h1>

      
      </div>

      <div className="d-flex flex-column  text-center">
          <div className="m-5">
          <TextField id="outlined-basic"  variant="outlined" 
          value={email}
           onChange={(e)=>setEmail(e.target.value)}
 />

          </div>

          <div >

<TextField id="outlined-basic"  variant="outlined"  
value={userName}
           onChange={(e)=>setUserName(e.target.value)}

/>
            <div>
            
            </div>
<div className="m-5">

<LocalizationProvider dateAdapter={AdapterDateFns}>

<DatePicker
  label="Birthday"
  onChange={(newValue) => {
    setBirthday(newValue);
  }}
  value={birthday}
  renderInput={(params) => <TextField {...params} />}
/>
</LocalizationProvider>
          </div>
          <div className="mx-60">
              
        <Button fullWidth variant="contained" type="submit">
          Submit
        </Button>
          </div>

          <div className="mx-60 mt-5">
              
              <Button fullWidth color="error" variant="contained" type="submit">
                Delete Account
              </Button>
                </div>
   </div>

      </div>
        </div>)}